@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI",
        "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas,
        "Courier New", monospace;
}

.timer-container {
    margin: 0 20px;
}
.timer-container h1 {
    text-align: center;
}
.timer-container h3 {
    margin-top: 0;
    /* margin-block-start: 0; */
}
h2 {
    margin-top: 20px;
}
.controls {
}

.controls button {
    background-color: transparent;
    padding: 10px;
    margin: 15px;
    font-size: 20px;
    border: none;
    cursor: pointer;
}

.controls span {
    font-size: 40px;
    margin: 0 10px;
}

.control-icon {
    pointer-events: none;
}

.clock-container {
    background-color: transparent;
    margin: 20px 0px;
    padding: 20px;
    border: solid 4px #f9f7f7;

    /* border-top: 4px solid #e8544a; */

    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    display: flex;
    /*align-self: center;
    */
    flex-flow: column;
    justify-content: center;
    width: 33vw;
    height: 33vw;
}

.clock-container h1 {
    text-align: center;
    background-color: transparent;
    margin: 0;
    font-size: 3.85vw;
}
.clock-container span {
    font-size: 9vw;
}
.controls {
    background-color: transparent;
}

.controls button {
    background-color: transparent;
    padding: 0.6vw;
    margin: 1vw;
    font-size: 1.4vw;
    border: none;
    cursor: pointer;
    outline: none;
}
#start_stop svg path {
    color: #f9f7f7;
}
#reset {
    position: absolute;
    margin: 1.6vw 1.4vw;
}
.controls span {
    font-size: 40px;
    margin: 0 10px;
}

.control-icon {
    pointer-events: none;
}

#time-left {
    color: #f9f7f7;
    text-shadow: 0 0 9px;
    background-color: transparent;
}

nav {
    height: 100%;
    width: 100px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    /* background-color: #111; */
    border: black solid 2px;
    overflow-x: hidden;
    /* padding-top: 29px; */
}

nav ul {
    list-style-type: none;
    padding: 0px;
}

nav ul li a {
    /* width: 100%; */

    text-decoration: none;
    color: #000;
    font-size: 0.7em;
}
nav ul li {
    margin: 46px 0px;
}

form {
    display: flex;
}
form input,
form button {
    padding: 0.5rem;
    font-size: 1.2rem;
    border: none;
    background: #dcdcdc;
    vertical-align: middle;
    text-align: start;
    outline: none;
}
form input {
    flex: 1 1;
    min-width: 30vw;
    color: #000;
    border-radius: 3px 0px 0px 3px;
    -webkit-border-radius: 3px 0px 0px 3px;
    -moz-border-radius: 3px 0px 0px 3px;
    -ms-border-radius: 3px 0px 0px 3px;
    -o-border-radius: 3px 0px 0px 3px;
}
form button {
    color: #000;
    font-size: 1.8rem;
    padding: 0.09rem 0.5rem;
    cursor: pointer;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    border-radius: 0px 3px 3px 0px;
    -webkit-border-radius: 0px 3px 3px 0px;
    -moz-border-radius: 0px 3px 3px 0px;
    -ms-border-radius: 0px 3px 3px 0px;
    -o-border-radius: 0px 3px 3px 0px;
}
form button:hover {
    background-color: #4e4e4e;
}

form button:hover svg path {
    /* background-color: #4e4e4e; */
    color: #dcdcdc;
}

form button svg {
    background: transparent;
}
form button svg path {
    color: #000;
}

.completed {
    text-decoration: line-through;
    opacity: 0.4;
}

* {
    text-align: center;
}
.Todo-List-Main {
    max-width: 50vw;
}
.input-container {
    display: flex;
    align-items: center;
}
form {
    flex: 1 1;
}
#select-down {
    position: relative;
    right: 1.8rem;
    margin-right: -1.2rem;
    top: 0.1rem;
    pointer-events: none;
    font-size: 1.5rem;
    background-color: #dcdcdc;
}
#select-down svg {
    background-color: #dcdcdc;
}
#select-down svg path {
    color: #000;
}

select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    outline: none;
    border: none;
    color: #000;

    background-color: #dcdcdc;
    cursor: pointer;
    font-size: 1.2rem;
    padding: 0.5rem;
    padding-right: 2rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.select {
    margin: 1rem 0rem 1rem 1rem;
    position: relative;
    overflow: hidden;
    text-align: start;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
/* .select::after {
    content: "\25BC";
    position: absolute;
    color: #000;
    font-size: 1.2rem;
    padding: 0.5rem;
    top: 4px;
    right: 0;
} */
.todo-container {
    display: flex;
    justify-content: center;
    align-items: center;
}
.todo-list {
    min-width: 30%;
    width: 100%;
    max-width: 100%;

    list-style: none;
    padding: 0;
}
.Todo {
    margin: 0.5rem 0;
    background: #dcdcdc;
    color: #000;
    font-size: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
.Todo li {
    background: #dcdcdc;
    color: #000;
    flex: 1 1;
    text-align: start;
    /* overflow: scroll; */
}
.trash-btn,
.complete-btn {
    /* background: #7e7e7e; */
    color: #dcdcdc;
    border: none;
    padding: 0.8rem;
    cursor: pointer;
    font-size: 1rem;
}
.trash-btn,
.trash-btn svg {
    background: #7e7e7e;
    border-radius: 0 3px 3px 0;
    -webkit-border-radius: 0 3px 3px 0;
    -moz-border-radius: 0 3px 3px 0;
    -ms-border-radius: 0 3px 3px 0;
    -o-border-radius: 0 3px 3px 0;
}
.complete-btn,
.complete-btn svg {
    background: #9e9e9e;
}
.trash-btn svg path,
.complete-btn svg path {
    color: #dcdcdc;
}
.todo-item {
    padding: 0 0.5rem;
    overflow: auto;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
@media screen and (max-width: 1000px) {
    form input,
    form button {
        padding: 0.3rem;
        font-size: 1.2rem;
    }
    select {
        font-size: 1rem;
        padding: 0.4rem;
    }
    #select-down {
        font-size: 1rem;
        right: 1.4rem;
        margin-right: -0.9rem;
    }
    form input {
        width: 32vw;
    }
    .Todo {
        font-size: 1.1rem;
    }
    .trash-btn,
    .complete-btn {
        padding: 0.5rem;
    }
}
@media screen and (max-width: 700px) {
    nav {
        height: auto;
        width: 100%;
        position: fixed;
        /* z-index: 1; */
        top: auto;
        left: 0;
        bottom: 0;
    }
    nav ul {
        display: flex;
        justify-content: space-around;
    }
    nav ul li {
        margin: 0;
    }
    .Todo-List-Main {
        max-width: 90vw;
    }
    .input-container {
        flex-flow: row wrap;
    }
    form input,
    form button {
        padding: 0.3rem;
        font-size: 1.2rem;
    }
    .select {
        margin: 1rem 0rem 0rem 0rem;
    }
    select {
        font-size: 1rem;
        padding: 0.4rem;
        padding-right: 2rem;
    }
    #select-down {
        font-size: 1rem;
        top: 0.5rem;
        right: 1.4rem;
        margin-right: -0.9rem;
    }
    form input {
        width: 32vw;
    }
    .Todo {
        font-size: 1.1rem;
    }
    .trash-btn,
    .complete-btn {
        padding: 0.5rem;
    }
}

@media screen and (max-width: 400px) {
    .Todo-List-Main {
        max-width: 80vw;
    }
    .input-container {
        flex-flow: row wrap;
    }
    form input,
    form button {
        outline: none;
        padding: 0.3rem;
        font-size: 1.2rem;
    }
    .select {
        margin: 1rem 0rem 0rem 0rem;
    }
    select {
        font-size: 0.8rem;
        padding: 0.4rem;
        padding-right: 2rem;
    }
    #select-down {
        font-size: 1rem;
        top: 0.5rem;
        right: 1.4rem;
        margin-right: -0.9rem;
    }
    form input {
        width: 32vw;
        outline: none;
        border: none;
    }
    .Todo {
        font-size: 0.8rem;
    }
    .trash-btn,
    .complete-btn {
        padding: 0.4rem;
    }
    #reset {
        margin: 2vw 4vw;
    }
}

* {
    box-sizing: border-box;
    /* background-color: #70a1ff; */
    background-color: #000;
    color: #9e9e9e;
    font-family: "Noto Sans", sans-serif;
}

.App,
.ToDoList {
    text-align: center;
}
body {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100vw;
}
.Clock {
    display: flex;

    justify-content: center;
    margin: 20px 0px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.Counters {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: space-around; */
    margin: 20px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

#break-label,
#session-label {
    margin: 10px 20px;
}
@media screen and (max-width: 1000px) {
    body {
        width: 100%;
        display: flex;
        flex-flow: column-reverse;
    }
    .Counters {
        flex-flow: row wrap;
        justify-content: center;
        margin: 0 20px;
    }
    .Counters h2 {
        width: 100%;
        margin: 0;
    }
    .clock-container {
        width: 70vw;
        height: 70vw;
    }
    .clock-container span {
        font-size: 20vw;
    }
    .controls button {
        margin: 1vw;
        font-size: 3vw;
    }
    .clock-container h1 {
        font-size: 8vw;
    }
}

